import { circleLogic } from './circle.js';
import { rectangleLogic } from './rectangle.js';
import { triangleLogic } from './triangle.js';
export const serializeShape = (shape) => {
    // Shapes are just primitive data types, can be serialized as they are.
    return shape;
};
export const deserializeShape = (serializedShape) => {
    // Shapes are just primitive data types, can be deserialized as they are.
    return serializedShape;
};
export const getRandomPositionInsideShape = (shape) => {
    return selectShapeLogic(shape).getRandomPosition(shape);
};
export const getRandomPositionOnEdge = (shape) => {
    return selectShapeLogic(shape).getRandomEdgePosition(shape);
};
export const shapeContainsPosition = (shape, position) => {
    return selectShapeLogic(shape).containsPosition(shape, position);
};
const selectShapeLogic = (shape) => {
    switch (shape.type) {
        case "triangle":
            return triangleLogic;
        case "rectangle":
            return rectangleLogic;
        case "circle":
            return circleLogic;
    }
};
